@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
@import "_variables.scss";

:root {
    --padding: 1vh;
}

html, body {
    background-color: var(--debugBg);
    margin: 0;
    font-size: 1.75vh;
    font-family: "Open Sans";
    user-select: none;
    color: var(--textCol);

    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow-y: auto;
}

.historyTitle {
    font-size: 3vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
    padding-left: calc(var(--padding) / 2);
    padding-top: calc(var(--padding) / 2);
    align-self: flex-start;
}

.historySeparator {
    justify-content: center;
    flex-direction: column;
    align-items: center;
    display: block;
    background-color: var(--borderCol);
    padding: 0;
    margin: 0;
    margin-top: 0.25em;
    margin-bottom: 5.5vh;
    margin-left: calc((100vw - (100vw - var(--padding) * 4)) / 2);
    width: calc(100vw - var(--padding) * 4);
    height: var(--borderThickness);
}

.history-wipe {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 6.5vh;
    width: 80vw;
    height: 5vh;
    background-color: transparent;
    border: var(--borderThickness) solid var(--hoverBorderCol);
    color: var(--textCol);
    font-size: 2vh;
    padding: 0.5em 1em;
    transition: background-color 0.3s ease;
    border-radius: var(--borderRad);
    text-align: center;
    box-shadow: var(--hoverShadowRad) var(--hoverShadowCol);
    &:hover {
        background-color: color-mix(in srgb, var(--hoverBorderCol) 20%, transparent);
        text-shadow: 0px 0px 6px var(--hoverBorderCol);
        cursor: pointer;
    }
}

.historyContainer {
    flex-direction: column;
    justify-content: left;
    font-size: 6vw;
}

.history-item {
    outline: none;
    border: none;
    background: none;
    color: var(--textCol);
}

.history-entry {
    transform: translateY(1vh);
    margin: 10px;
    padding: 5px;
    margin-left: var(--padding);
    left: 0 !important;
}

.history-query {
    font-weight: bold;
    cursor: pointer;
}

.history-date,
.history-time {
    color: var(--textColSec);
}
